import React, {useRef} from 'react'

const Index = (props) => {
    const UserInputRef = React.useRef(null)
    return <div className={"Page-login"}>
        <div className="box" style={{height: '88vh', width: window.innerWidth}}><h1>Login</h1>
            <input
                ref={UserInputRef} type="text" placeholder="Username"/>
            <input type="password" placeholder="Password"/>
            <a className="submit" href="#" onClick={() => {
                sessionStorage.setItem("userInfo", JSON.stringify({id: UserInputRef.current.value}));
                const lastUrlLocation = JSON.parse(sessionStorage.getItem("lastUrlLocation")) || {};
                sessionStorage.removeItem("lastUrlLocation");
                if ((!lastUrlLocation.pathname)||["/login","/"].includes(lastUrlLocation.pathname)) {
                    props.history.push("Home")
                } else if (lastUrlLocation) {
                    props.history.push(lastUrlLocation)
                }
            }}>Login</a></div>
    </div>
}
export default Index