import React, {useState, useEffect} from 'react';

const useBindHook = (initialValue) => {
    const [value, setValue] = useState(initialValue || "");
    const onChange = val => {
        setValue(val)
    };
    return {value, onChange};
}
const Index = (props) => {
    const {value, onChange} = useBindHook(true);
    const click = () => {
        onChange(!value)
    }
    return <div>         {value ? <div onClick={click}>show</div> : <div onClick={click}>unShow</div>}
        <hr/>
        <div style={{
            width: window.innerWidth,
            height: window.innerHeight * 0.5,
            background: "#ddd"
        }}>             {value ?
            <div className="PersonAbout-wrap"><img src="../images//微信头像.jpg" alt="" className="box-img"/><h1>lilili</h1>
                <h5>Front-end development</h5>                     <p></p>
                <ul>
                    <li><a href="https://public-1256942570.cos.ap-chengdu.myqcloud.com/qrcode.jpg"><i
                        className="fab fa-weixin"></i></a></li>
                    <li><a href="https://public-1256942570.cos.ap-chengdu.myqcloud.com/qq_qccode.jpg"><i
                        className="fab fa-qq"></i></a></li>
                    <li><a href="https://github.com/yyeexin"><i className="fab fa-github"></i></a></li>
                </ul>
            </div> : <div style={{width: "100%", height: "100%", background: "#667711"}}>more ...</div>}      </div>
    </div>
}
export default Index;